    padding-left: 20px;
    position: fixed;
    font-size: 15px;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    font-size: 25px;
    z-index: 9999;
    display: flex;
    justify-content: space-between;
  }

  nav ul {
    position: fixed;
    bottom: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
    background-color: #343a40;

    height: 50px;
    display: flex;
    align-items: center;
    padding: 5px 0;
    z-index: 999;
  }
  nav li a .icons {
    color: #fff;
