.modelInfoWrapper {
  height: 100vh;
  width: 100%;
  background: #0e0e0e77;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999999;
}
.modelInfoWrapper .actualModel {
  width: 520px;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  background: var(--cardbg);
  /*background: var(--browsebg);*/
  border: 1px solid var(--cardborder);
  padding: 15px 15px;
  border-radius: 5px;
  z-index: 9999999;
  box-shadow: var(--shadow);
  -webkit-animation: model 3.5s ease-in-out 1;
          animation: model 3.5s ease-in-out 1;
}
@-webkit-keyframes model {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
  10% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}
@keyframes model {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
  10% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

.actualModel .modelHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 15px;
  border-bottom: 1px solid var(--cardborder);
}
.modelHeader .modelTitle {
  font-size: 20px;
  font-weight: bolder;
  color: var(--headingcolor);
  max-width: 90%;
}
.modelHeader .cross {
  height: 35px;
  width: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: var(--browsebg);
  cursor: pointer;
}
.modelHeader .cross:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.modelHeader i {
  color: var(--lightcolor);
  font-size: 20px;
}

.actualModel .modelMessage {
  font-size: 15px;
  padding: 10px 0;
  color: var(--headingcolor);
  max-width: 90%;
}
.actualModel .buttonGrpModel {
  padding: 20px 0 0 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.buttonGrpModel button {
  padding: 7px 25px;
  border-radius: 5px;
  background: var(--button);
  color: var(--btntext);
  cursor: pointer;
}
.buttonGrpModel .btn2 {
  background: transparent;
  color: var(--button);
  padding: 7px 20px;
  margin-right: 10px;
}
.buttonGrpModel .btn2:hover {
  background: var(--browsebg);
}
.buttonGrpModel :not(.btn2):focus {
  -webkit-transform: scale(0.93);
          transform: scale(0.93);
}
@media screen and (max-width: 768px) {
  .modelInfoWrapper {
    padding: 0 15px;
  }
  .modelHeader .modelTitle {
    font-size: 20px;
  }
  .actualModel .modelMessage {
    max-width: 95%;
  }
}
.buttonGrpModel small {
  color: var(--lightcolor);
}
@media screen and (max-width: 390px) {
  .buttonGrpModel small {
    color: var(--lightcolor);
    font-size: 13px;
  }
  .buttonGrpModel button {
    padding: 7px 12px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
}
footer {
  width: 100%;
  margin-top: auto;
  background: var(--footerbg);
  border-top: 1px solid var(--cardborder);
}
footer .content {
  max-width: 1350px;
  margin: auto;
  padding: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
footer > * {
  font-size: 13px;
}
footer .content p,
footer a {
  color: var(--footertxt);
}

footer .content .box {
  width: 33%;
  /*transition: all 0.4s ease;*/
}
footer .content .topic {
  font-size: 18px;
  color: var(--footertxt);
  margin-bottom: 16px;
}
.weather {
  margin-top: 20px;
}
.weather .weatherheading {
  font-size: 18px;
  margin-bottom: 5px;
}
footer .content p {
  text-align: left;
}
footer .content .lower .topic {
  margin: 24px 0 5px 0;
}
footer .content .lower i {
  padding-right: 16px;
}
footer .content .middle {
  padding-left: 80px;
}
footer .content .middle a {
  line-height: 32px;
}

footer .content .media-icons a {
  font-size: 16px;
  height: 45px;
  width: 45px;
  display: inline-block;
  text-align: center;
  line-height: 43px;
  border-radius: 5px;
  border: 2px solid #222222;
  margin: 30px 5px 0 0;
}
/*ahah */

footer .bottom {
  width: 100%;
  text-align: right;
  color: var(--footertxt);
  padding: 0 40px 5px 0;
}
.bottom p {
  color: var(--footertxt);
}
.cncttxt {
  color: var(--footertxt);
}
footer .content .right textarea {
  height: 80px;
  width: 100%;
  outline: none;
  color: var(--textareacolor);
  background: var(--footertxtarea);
  border-radius: 5px;
  padding-left: 10px;
  font-size: 17px;
  border: 2px solid #222222;
  resize: none;
}
footer .content .right button {
  height: 42px;
  width: 100%;
  font-size: 18px;
  color: var(--btntext);
  background: var(--button);
  outline: none;
  border: 0;
  border-radius: 5px;
  letter-spacing: 1px;
  cursor: pointer;
  margin-top: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.disabled {
  cursor: not-allowed;
  background: grey;
}

@media (max-width: 1100px) {
  footer .content .middle {
    padding-left: 50px;
  }
}
@media (max-width: 950px) {
  footer .content .box {
    width: 50%;
  }
  .content .right {
    margin-top: 40px;
  }
  footer {
    padding-bottom: 50px;
  }
}
@media (max-width: 560px) {
  footer {
    position: relative;
  }
  footer .content .box {
    width: 100%;
    margin-top: 30px;
  }
  footer .content .middle {
    padding-left: 0;
  }

  footer .bottom {
    text-align: left;
    padding: 0 40px 5px 25px;
  }
}

